import targetImage from "../assets/img/target.svg";
import brushImage from "../assets/img/brush.svg";
import energyImage from "../assets/img/energy.svg";

import '../styles/subservice.css'

export default function SubService() {
    return (
      <section className="sub-service">
        <div className="items">
          <div className="sub-box">
            <div className="sub-img">
              <img src={targetImage} alt="Target" />
            </div>
            <h3>Scalable and Reliable Software</h3>
            <p>Unlock your business potential with scalable and reliable software solutions.</p>
          </div>
  
          <div className="sub-box">
            <div className="sub-img">
              <img src={brushImage} alt="Brush" />
            </div>
            <h3>High Quality</h3>
            <p>We create high-quality products and uphold stringent quality standards to ensure exceptional deliverables.</p>
          </div>
  
          <div className="sub-box">
            <div className="sub-img">
              <img src={energyImage} alt="Energy" />
            </div>
            <h3>Innovate Ideas </h3>
            <p>We transform innovative ideas into cutting-edge products that disrupt the market and drive business growth.</p>
          </div>
        </div>
      </section>
    );
  }