import React, { useState } from 'react';

import logo from '../assets/img/logo.png';
import '../styles/header.css';
import { Link } from 'react-router-dom';

function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleToggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <header>
      <Link to="/Portfolio" className="logo col-md-2">
        <img src={logo} alt="Logo" />
      </Link>

      <ul className={`col-md-6 headerNavList ${isMenuOpen ? 'active' : ''}`}>
        <li><Link to="/Portfolio">Home</Link></li>
        <li><Link to="/Portfolio/about">About</Link></li>
        <li><Link to="/Portfolio/certification">Certifications</Link></li>
        <li><Link to="/Portfolio/services">Services</Link></li>
        <li><Link to="/Portfolio/awards">Awards</Link></li>
        <li><Link to="/Portfolio/contact">Contact</Link></li>
      </ul>


      <div className='col-md-2'></div>

      <div className="toggle_btn" onClick={handleToggleMenu}>
        <i className={`fa-solid ${isMenuOpen ? 'fa-xmark' : 'fa-bars'}`}></i>
      </div>
    </header>
  );
}

export default Header;
