import { Link } from 'react-router-dom';

import Home from './Home';

import AboutImage from '../assets/img/about.jpg';
import '../styles/about.css';

export default function About(props) {
  return props.homePage ? homeAbout() : AboutPage();

  function homeAbout() {
    return (
      <section className="about" id="about">
        <div className="about-img">
          <img src={AboutImage} alt="About" />
        </div>

        <div className="about-text">
          <h3>// WHO I AM</h3>
          <h2>I build quality digital products, quickly and efficiently.</h2>
          <p>I am a skilled Full Stack developer who specializes in helping individuals and companies innovate, streamline, and succeed in the digital landscape. With your goals as my top priority, I leverage my extensive experience and expertise to deliver tailored technology solutions that quickly realize value for your personal endeavors.</p>
          <Link to='/Portfolio/about' className="primary-btn">Learn More</Link>
        </div>
      </section>
    );
  }

  function AboutPage() {

    return (
      <section id="about">
        <div className="mb-5">
          <Home />
        </div>

        <div className='about'>

          <div className="about-text mt-md-3">
            <h3>// WHO I AM</h3>
            <h2>I build quality digital products, quickly and efficiently.</h2>

            <p> Hi there! I'm a passionate Software Engineer with a strong background in modern technologies and software development. Holding a degree in Software Engineering from Sir Syed University, I bring a wealth of knowledge and expertise to the table .</p>
            <p> Currently, I contribute my skills as a Software Engineer at Techsol 360, where I actively participate in developing robust and innovative software solutions. With proficiency in utilizing cutting-edge technologies like Flutter, MERN Stack (MongoDB, Express.js, React.js, Node.js), and ASP .NET, I strive to create efficient and scalable applications that exceed client expectations.</p>
            <p> With a keen eye for detail, I ensure adherence to software development standards and implement rigorous quality assurance processes throughout the software development lifecycle. My commitment to excellence guarantees that every project I undertake meets the highest standards of quality and reliability.</p>

          </div>

          <div className="about-text">

            <p> Additionally, I have a solid foundation in data analytics, having acquired certification as a Junior Data Analyst from Google. I possess versatile skills in data collection, transformation, organization, and visualization, enabling me to make informed data-driven decisions. I am well-versed in MS Excel/Google Sheets, SQL, Tableau, and R, and I bring excellent communication, analytical, and critical thinking abilities to the table.</p>
            <p>If you're looking for a versatile Software Engineer who excels in both development and data analytics, I'm here to help. Let's connect and discuss how I can leverage my expertise to contribute to your organization's success, whether it's building web applications with MERN or developing mobile applications with Flutter. Together, we can create exceptional software solutions that drive innovation and deliver results!</p>

          </div>
        </div>
      </section>
    );
  }
}
