import React from 'react';
import './App.css';

import Header from './components/Header';
import HomePage from './pages/HomePage'
import CertificationPage from './pages/CertificationPage';
import AwardsPage from './pages/AwardPage'
import ServicePage from './pages/ServicePage';
import ContactPage from './pages/ContactPage';
import Footer from './components/Footer';
import About from './components/About';

import { BrowserRouter as Router, Routes, Route, Link, Navigate } from 'react-router-dom';


export default function App() {
  return (
    <React.Fragment>

      <Router>
        <Header />
        <Link to="/Portfolio" />
        <Routes>
          <Route exact path="/Portfolio" element={<HomePage />} />
          <Route path="/Portfolio/about" element={<About homePage={false} />} />
          <Route path="/Portfolio/certification" element={<CertificationPage />} />
          <Route path="/Portfolio/services" element={<ServicePage />} />
          <Route path="/Portfolio/awards" element={<AwardsPage />} />
          <Route path="/Portfolio/contact" element={<ContactPage />} />
          <Route path="*" element={<Navigate to="/Portfolio" replace />} />
        </Routes>
        <Footer />
      </Router>

    </React.Fragment>
  );
}

