import React from 'react';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';

function CustomAlert({ open, onClose, type }) {
  let message, severity;

  if (type === 'success') {
    message = 'Your contact request has been initiated. We will get back to you soon.';
    severity = 'success';
  } else if (type === 'error') {
    message = 'An error occurred while submitting your contact request.';
    severity = 'error';
  }

  return (
    <Snackbar open={open} autoHideDuration={5000} onClose={onClose}>
      <Alert onClose={onClose} severity={severity}>
        {message}
      </Alert>
    </Snackbar>
  );
}

export default CustomAlert;
