import { useState, useEffect } from 'react';
import MainImage from '../assets/img/circle-main.png';
import '../styles/home.css';
import { Row } from 'react-bootstrap';

export default function Home() {
  const linkedin = "https://www.linkedin.com/in/muhammad-hamza-suleman/";
  const [isWideScreen, setIsWideScreen] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsWideScreen(window.innerWidth > 540);
    };

    handleResize();

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (

    isWideScreen ?
      onWideScreen()
      : onSmallScreen()
  );

  function onSmallScreen() {
    return <section className="home" id="home">

      <div className='home-container'>
        <Row>
          <div className="home-img">
            <img src={MainImage} alt="Main" />
          </div>
          <div className="home-text">
            <h3>Hello, I'm</h3>
            <h1>Muhammad Hamza Suleman</h1>
            <h5>A Full Stack Developer <span>From Karachi, Pakistan</span></h5>
            <p>I'm a full stack web and mobile developer living in Karachi, Pakistan, and I'm very passionate and dedicated to my work.</p>
          </div>
        </Row>
        <a href={linkedin} className="outlined-btn">
          <i className="bx bxl-linkedin"></i>
          LinkedIn
        </a>
      </div>
    </section>;
  }

  function onWideScreen() {
    return <section className="home" id="home">
      <div className="home-text">
        <h3>Hello, I'm</h3>
        <h1>Muhammad Hamza Suleman</h1>
        <h5>A Full Stack Developer <span>From Karachi, Pakistan</span></h5>
        <p>I'm a full stack web and mobile developer living in Karachi, Pakistan, and I'm very passionate and dedicated to my work.</p>

        <a href={linkedin} className="outlined-btn">
          <i className="bx bxl-linkedin"></i>
          LinkedIn
        </a>
      </div>

      <div className="home-img">
        <img src={MainImage} alt="Main" />
      </div>
    </section>;
  }
}