
import View from '../components/View.js';
import awards from '../assets/certificates and awards/awards.json';

import '../styles/pages/AwardsPage.css'

const AwardPage = () => {
    return(<Award awards={awards}/>);
}

export default AwardPage;

const Award = ({ awards }) => {

  const handleAwardClick = (pdfUrl) => {
    const fileExtension = pdfUrl.split('.').pop().toLowerCase();
    if (fileExtension === 'pdf') {
      window.open(pdfUrl, '_blank');
    }
  };  
  
    return (
      <section className='awards-container'>
        {awards.map(award => (
          <View item={award} handleFunction={() => handleAwardClick(process.env.PUBLIC_URL +"/"+ award.url)} />
        ))}
      </section>
    );
  };
  