import React, { useState } from 'react';
import certifications from '../assets/certificates and awards/certifications.json';
import specializations from '../assets/certificates and awards/specializations.json';

import View from '../components/View';
import '../styles/pages/CertificationPage.css';


const CertificationPage = () => {
  const [activeTab, setActiveTab] = useState('certificates');

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  return (
    <div className="certification-page">
      <div className="tab-bar">
        <a role='button'
          className={`mx-3 ${activeTab === 'certificates' ? 'active' : ''}`}
          onClick={() => handleTabChange('certificates')}
        >
          Certificates
        </a>
        <a role='button'
          className={`mx-3 ${activeTab === 'specializations' ? 'active' : ''}`}
          onClick={() => handleTabChange('specializations')}
        >
          Specializations
        </a>
      </div>

      <div className="tab-content">
        {activeTab === 'certificates' && <Certification certifications={certifications} />}
        {activeTab === 'specializations' && <Certification certifications={specializations} />}
      </div>
    </div>
  );
};

export default CertificationPage;


const Certification = ({ certifications }) => {
  const handleCertificationClick = (pdfUrl) => {
    window.open(pdfUrl, '_blank');
  };

  return (
    <section className='certifications-container'>
      {certifications.map(certification => (
        <View 
        item={certification} 
        showButton={true}
        handleFunction={() => handleCertificationClick(process.env.PUBLIC_URL +"/"+ certification.url)} />
      ))}
    </section>
  );
};
