
import '../styles/view.css'

const View = ({ item, handleFunction, showButton }) => {

    return (
      (
        item.id % 2 === 0 ? (
          <LeftThumbnailView item={item} />
        ) : (
          <RightThumbnailView item={item} />
        )
      )
    );
  
    function LeftThumbnailView({ item }) {
       const buttonElement = showButton ? 
       <button className='primary-btn d-none d-lg-block' onClick={handleFunction}>Open PDF</button> :
       <div></div>
       
      return <div className='view-grid view-left' key={item.id}>
        <div className='item-thumbnail' onClick={handleFunction}>
          <img src={process.env.PUBLIC_URL +"/"+ item.thumbnail} alt={item.title} />
        </div>
        <div className='details'>
          <h2 className='item-title'>{item.title}</h2>
          <p className='item-text'>{item.description}</p>
          {buttonElement}
        </div>
      </div>;
    }
  
    function RightThumbnailView({ item }) {
        const buttonElement = showButton ? 
        <button className='primary-btn d-none d-lg-block' onClick={handleFunction}>Open PDF</button> :
       <div></div>

      return <div className='view-grid view-right' key={item.id}>
  
        <div className='details order-last order-lg-first'>
          <h2 className='item-title'>{item.title}</h2>
          <p className='item-text'>{item.description}</p>
         {buttonElement}
         </div>
  
        <div className='item-thumbnail' onClick={handleFunction}>
          <img src={process.env.PUBLIC_URL +"/"+ item.thumbnail} alt={item.title} />
        </div>
      </div>;
    }
  }
  
  
  export default View;