import React, { useState } from 'react';
import { Container, Form, Row, Col, Button } from 'react-bootstrap';
import CustomAlert from './shared/custom_alert';
import '../styles/contact.css';

function Contact() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const [successAlertOpen, setSuccessAlertOpen] = useState(false);
  const [errorAlertOpen, setErrorAlertOpen] = useState(false);

  const handleNameChange = (e) => {
    setName(e.target.value);
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    await fetch('https://formspree.io/f/xrgwpopq', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        Name: name,
        Email: email,
        Message: message,
      }),
    })
      .then(response => response.json())
      .then(data => {
        data['ok'] ?
          setSuccessAlertOpen(true) :
          setErrorAlertOpen(true);
      })
      .catch(error => {
        console.error('Error submitting form:', error);
        setErrorAlertOpen(true);
      });


    setName('');
    setEmail('');
    setMessage('');
  };

  const closeAlert = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setSuccessAlertOpen(false);
    setErrorAlertOpen(false);
  };

  return (

    <section className="contact" id="contact">
      <Container>
        <div className="center">
          <h3 className="contact-title">Contact Me</h3>
          <p className="contact-subtitle">Your email address will not be published. Required fields are marked *</p>
        </div>

        <div className="action">
          <Form onSubmit={handleSubmit}>
            <Row>
              <Col md={6}>
                <Form.Group controlId="name">
                  <Form.Control
                    className="form-input"
                    type="text"
                    placeholder="Your Name *"
                    value={name}
                    onChange={handleNameChange}
                    required
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group controlId="email">
                  <Form.Control
                    className="form-input"
                    type="email"
                    placeholder="Your Email *"
                    value={email}
                    onChange={handleEmailChange}
                    required
                  />
                </Form.Group>
              </Col>
            </Row>

            <Form.Group controlId="message">
              <Form.Control
                className="form-input"
                as="textarea"
                rows={5}
                placeholder="Message .."
                value={message}
                onChange={handleMessageChange}
                required
              />
            </Form.Group>

            <Button variant="primary" type="submit" className="primary-btn">
              Get in Touch
            </Button>

          </Form>

          <CustomAlert open={successAlertOpen} onClose={closeAlert} type="success" />
          <CustomAlert open={errorAlertOpen} onClose={closeAlert} type="error" />
        </div>
      </Container>
    </section>

  );
}

export default Contact;

